___CSS_LOADER_EXPORT___.push([module.id, `/* src/renderer/components/WorkBlockItem.css */
.work-block-item {
    display: flex;
    align-items: flex-start; /* 垂直对齐方式改为顶部，适应多行文本 */
    padding: 10px 12px; /* 增加垂直 padding */
    background-color: #333842;
    border-radius: 4px;
    gap: 15px;
    min-height: 40px; /* 设定一个最小高度 */
}

.work-block-time {
    font-weight: bold;
    color: #999;
    min-width: 50px;
    padding-top: 2px; /* 微调时间戳的垂直位置 */
    line-height: 1.4; /* 确保时间戳行高一致 */
}

.work-block-text {
    flex-grow: 1;
    cursor: pointer;
    padding: 2px 0; /* 微调 padding */
    /* white-space: nowrap; */ /* 移除，允许换行 */
    /* overflow: hidden; */ /* 移除 */
    /* text-overflow: ellipsis; */ /* 移除 */
    word-break: break-word; /* 允许在单词内换行 */
    line-height: 1.4; /* 设置行高 */
    white-space: pre-wrap; /* 保留空格和换行符 */
}
.work-block-text:hover {
    background-color: #404552;
}


.work-block-edit-input {
    flex-grow: 1;
    padding: 4px 6px;
    border: 1px solid #61dafb;
    background-color: #282c34;
    color: white;
    border-radius: 3px;
    outline: none;
    font-family: inherit;
    font-size: inherit;
    line-height: 1.4;
    /* 对于 textarea 的特定样式 */
    resize: none; /* 禁止调整大小，高度由 JS 控制 */
    overflow-y: hidden; /* 隐藏滚动条，高度由 JS 控制 */
    min-height: calc(1.4em + 8px); /* 最小高度约为一行文本加 padding */
    box-sizing: border-box; /* 确保 padding 和 border 包含在高度内 */
} `, "",{"version":3,"sources":["webpack://./src/renderer/components/WorkBlockItem.css"],"names":[],"mappings":"AAAA,8CAA8C;AAC9C;IACI,aAAa;IACb,uBAAuB,EAAE,sBAAsB;IAC/C,kBAAkB,EAAE,iBAAiB;IACrC,yBAAyB;IACzB,kBAAkB;IAClB,SAAS;IACT,gBAAgB,EAAE,aAAa;AACnC;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,eAAe;IACf,gBAAgB,EAAE,eAAe;IACjC,gBAAgB,EAAE,cAAc;AACpC;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,cAAc,EAAE,eAAe;IAC/B,yBAAyB,EAAE,YAAY;IACvC,sBAAsB,EAAE,OAAO;IAC/B,6BAA6B,EAAE,OAAO;IACtC,sBAAsB,EAAE,aAAa;IACrC,gBAAgB,EAAE,SAAS;IAC3B,qBAAqB,EAAE,aAAa;AACxC;AACA;IACI,yBAAyB;AAC7B;;;AAGA;IACI,YAAY;IACZ,gBAAgB;IAChB,yBAAyB;IACzB,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,aAAa;IACb,oBAAoB;IACpB,kBAAkB;IAClB,gBAAgB;IAChB,sBAAsB;IACtB,YAAY,EAAE,qBAAqB;IACnC,kBAAkB,EAAE,oBAAoB;IACxC,6BAA6B,EAAE,wBAAwB;IACvD,sBAAsB,EAAE,+BAA+B;AAC3D","sourcesContent":["/* src/renderer/components/WorkBlockItem.css */\r\n.work-block-item {\r\n    display: flex;\r\n    align-items: flex-start; /* 垂直对齐方式改为顶部，适应多行文本 */\r\n    padding: 10px 12px; /* 增加垂直 padding */\r\n    background-color: #333842;\r\n    border-radius: 4px;\r\n    gap: 15px;\r\n    min-height: 40px; /* 设定一个最小高度 */\r\n}\r\n\r\n.work-block-time {\r\n    font-weight: bold;\r\n    color: #999;\r\n    min-width: 50px;\r\n    padding-top: 2px; /* 微调时间戳的垂直位置 */\r\n    line-height: 1.4; /* 确保时间戳行高一致 */\r\n}\r\n\r\n.work-block-text {\r\n    flex-grow: 1;\r\n    cursor: pointer;\r\n    padding: 2px 0; /* 微调 padding */\r\n    /* white-space: nowrap; */ /* 移除，允许换行 */\r\n    /* overflow: hidden; */ /* 移除 */\r\n    /* text-overflow: ellipsis; */ /* 移除 */\r\n    word-break: break-word; /* 允许在单词内换行 */\r\n    line-height: 1.4; /* 设置行高 */\r\n    white-space: pre-wrap; /* 保留空格和换行符 */\r\n}\r\n.work-block-text:hover {\r\n    background-color: #404552;\r\n}\r\n\r\n\r\n.work-block-edit-input {\r\n    flex-grow: 1;\r\n    padding: 4px 6px;\r\n    border: 1px solid #61dafb;\r\n    background-color: #282c34;\r\n    color: white;\r\n    border-radius: 3px;\r\n    outline: none;\r\n    font-